}
.layout.center,
.layout.center-center{
-ms-flex-align:center;
align-items:center;
}
.layout.end{
-ms-flex-align:end;
align-items:flex-end;
}
/*alignmentinmainaxis*/
.layout.start-justified{
-ms-flex-pack:start;
justify-content:flex-start;
}
.layout.center-justified,
.layout.center-center{
-ms-flex-pack:center;
justify-content:center;
}
.layout.end-justified{
-ms-flex-pack:end;
